export var currModuleName = ""
var currBpmnFileName = ""

export function getCurrBpmnFileName() {return currBpmnFileName}
export function setCurrBpmnFileName(bpmnFileName) {currBpmnFileName = bpmnFileName}

export function getRequestParamValue(location, name){
  if(name=(new RegExp('[?&]'+encodeURIComponent(name)+'=([^&]*)')).exec(location.search))
    return decodeURIComponent(name[1]);
  else
    return null;
}

export function resolveReplacementDefs(replacementsParam) {
  let replacementDefs = [];
  if (!replacementsParam || typeof replacementsParam !== 'string') return replacementDefs;

  let replacements = replacementsParam.split('_2_33_2_');
  // if (replacements) {
    replacements.forEach(r => {
      let tokens = r.split('_1_22_1_');
      if (tokens && tokens.length && tokens.length >1 ) {
        replacementDefs.push({'search': tokens[0], 'replace': tokens[1]})
      }
    })
  //}

  return replacementDefs;
}

export const isObject = (value) => (!!value) && (typeof value === 'object') && !Array.isArray(value)
export const isIn = (value, ...rest) => (!!value) && rest.includes(value);
