import {
  isAny
} from 'bpmn-js/lib/features/modeling/util/ModelingUtil';

import { appendSendSmsElements } from './PlateauActions';

import {
  assign
} from 'min-dash';

export default class PlateauContextPad {
  constructor(config, contextPad, create, elementFactory, injector, translate, elementRegistry, modeling) {
    this.create = create;
    this.elementFactory = elementFactory;
    this.translate = translate;
    this.elementRegistry = elementRegistry;
    this.modeling = modeling;

    if (config.autoPlace !== false) {
      this.autoPlace = injector.get('autoPlace', false);
    }

    contextPad.registerProvider(this);
  }

  getContextPadEntries(element) {
    const {
      autoPlace,
      create,
      elementFactory,
      translate,
      elementRegistry,
      modeling
    } = this;

    // appendSendSmsElements(event, element, elementFactory, elementRegistry, modeling, autoPlace);

    /*
    function appendServiceTask(event, element) {
      if (autoPlace) {
        const shape = elementFactory.createShape({ type: 'bpmn:ServiceTask' });
  
        autoPlace.append(element, shape);
      } else {
        appendServiceTaskStart(event, element);
      }
    }

    function appendServiceTaskStart(event) {
      const shape = elementFactory.createShape({ type: 'bpmn:ServiceTask' });
  
      create.start(event, shape, element);
    }
*/

    var businessObject = element.businessObject;
    let actions = {};
    if (isAny(businessObject, [ 'bpmn:Task'])) {
      assign(actions, {
        'append.service-task': {
          group: 'model',
          className: 'icon-custom-triangle',
          title: translate('Append Send Sms Elements'),
          action: {
            click: appendSendSmsElements,
            dragstart: appendSendSmsElements
          }
        }
      })
    }

    return actions;
  }
}

PlateauContextPad.$inject = [
  'config',
  'contextPad',
  'create',
  'elementFactory',
  'injector',
  'translate',
  'elementRegistry',
  'modeling'
];