import { appendSendSmsElements, appendPlateauServiceTask } from './PlateauActions';
import { openPlateauForm } from './PlateauFormExtension';

export default class PlateauPalette {
  constructor(create, elementFactory, palette, translate, selection, elementRegistry, modeling, injector, config) {
    this.create = create;
    this.elementFactory = elementFactory;
    this.translate = translate;
    this.selection = selection;
    this.elementRegistry = elementRegistry;
    this.modeling = modeling;
    this.injector = injector;
    this.config = config;

    if (config.autoPlace !== false) {
      this.autoPlace = injector.get('autoPlace', false);
    }

    palette.registerProvider(this);
  }

  getPaletteEntries(element) {
    const {
      create,
      elementFactory,
      translate,
      selection,
      elementRegistry,
      modeling,
      autoPlace
    } = this;

    async function createSendSmsElements(event) {

      // var selectedElements = modeler.get('selection').get();
      const selectedElements = selection.get();
      if (!selectedElements || !selectedElements.length) {
        alert('Bpmn komponenti seçiniz.');
        return;
      }

      const selectedElement = selectedElements[0];
      var paletteEvent = event;

      const smsSubjectDefs = [
        {
          "label": "Başvuru Alındı",
          "value": "smsApplicationBody"
        },
        {
          "label": "Başvuru Tamamlandı",
          "value": "smsBody"
        }
      ]

      const smsSchema = {
        "components": [
          {
            "id": "Select_SmsSubject",
            "key": "smsSubject",
            "label": "Sms Konusu",
            "type": "select",
            "values": smsSubjectDefs,
            "validate": {
              "required": true
            }
          },
          {
            "key": "submit",
            "label": "Tamam",
            "type": "button"
//          },
//          {
//            "action": "reset",
//            "key": "reset",
//            "label": "Sıfırla",
//            "type": "button"
          }
        ],
        "type": "default"
      };

      const exampleSchema = {
        "components": [
          {
            "key": "creditor",
            "label": "Müşteri",
            "type": "textfield",
            "validate": {
              "required": true
            }
          },
          {
            "key": "amount",
            "label": "Tutar",
            "type": "number",
            "validate": {
              "required": true
            }
          },
          {
            "description": "An invoice number in the format: C-123.",
            "key": "invoiceNumber",
            "label": "Fatura No",
            "type": "textfield",
            "validate": {
              "pattern": "^C-[0-9]+$"
            }
          },
          {
            "key": "approved",
            "label": "Onaylandı mı?",
            "type": "checkbox"
          },
          {
            "key": "approvedBy",
            "label": "Onay Veren",
            "type": "textfield"
          },
          {
            "key": "submit",
            "label": "Tamam",
            "type": "button"
          },
          {
            "action": "reset",
            "key": "reset",
            "label": "Sıfırla",
            "type": "button"
          }
        ],
        "type": "default"
      };

      const smsData = {
        smsSubject: 'Seyahat Başvuru'
      };

      const exampleData = {
        creditor: 'John Doe Company',
        amount: 100,
        invoiceNumber: 'C-369',
        approved: true,
        approvedBy: 'Sezgin Aytaç'
      };

      openPlateauForm(
        {
          lang: 'tr',
          schema: smsSchema,
          data: smsData,
          onSubmit: (event) => { 
            // alert(JSON.stringify(event.data));
            const selectedSmsSubjectLabel = smsSubjectDefs.find(item => item.value === event.data.smsSubject).label;
            appendSendSmsElements(paletteEvent, event.data, selectedSmsSubjectLabel, selectedElement, elementFactory, elementRegistry, modeling, autoPlace);
          }
        }
      )
    }
    
    async function createPlateauServiceTask(event) {

      // var selectedElements = modeler.get('selection').get();
      const selectedElements = selection.get();
      if (!selectedElements || !selectedElements.length) {
        alert('Select an element');
        return;
      }

      const selectedElement = selectedElements[0];
      var paletteEvent = event;
      const selectServiceDefs = [
        {
          "label": "Update Parameter",
          "value": "${flowRestCallComponent.init(execution).execute('http://cmbdemo/api/updateparameter', 'POST', formJson)}"
        },
        {
          "label": "Send SMS",
          "value": "${flowRestCallComponent.init(execution).execute('http://notification/api/sms/sms', 'POST' , smsBody)}"
        }
      ]

      const plateauServicesSchema = {
        "components": [
          {
            "id": "Select_Service",
            "key": "service",
            "label": "Plateau Servisi",
            "type": "select",
            "values": selectServiceDefs,
            "validate": {
              "required": true
            }
          },
          {
            "key": "submit",
            "label": "Tamam",
            "type": "button"
          }
        ],
        "type": "default"
      };

      const plateauServicesData = {
      };

      openPlateauForm(
        {
          lang: 'tr',
          schema: plateauServicesSchema,
          data: plateauServicesData, 
          onSubmit: (event) => { 
            // alert(JSON.stringify(event.data));
            const selectedServiceLabel = selectServiceDefs.find(item => item.value === event.data.service).label;
            if (selectedServiceLabel) {
              appendPlateauServiceTask(paletteEvent, event.data, selectedServiceLabel, selectedElement, elementFactory, elementRegistry, modeling, autoPlace);
            }
          }
        }
      )
    }

    return {
      'create.send-sms-elements-task': {
        group: 'activity',
        className: 'icon-sms',
        title: translate('Create Send Sms Elements'),
        action: {
          dragstart: createSendSmsElements,
          click: createSendSmsElements
        }
      },
      'create.plateau-service-task': {
        group: 'activity',
        className: 'bpmn-icon-service-task',
        title: translate('Create Plateau Service Task'),
        action: {
          dragstart: createPlateauServiceTask,
          click: createPlateauServiceTask
        }
      }
    }
  }
}

PlateauPalette.$inject = [
  'create',
  'elementFactory',
  'palette',
  'translate',
  'selection',
  'elementRegistry',
  'modeling',
  'injector',
  'config'
];