import translationsEn from './translations/translations-en.js'
import translationsTr from './translations/translations-tr.js'

import { getLanguage, supportedLanguages } from '../config'

const translations = {
  'en' : translationsEn,
  'tr' : translationsTr
}

export default function customTranslate(template, replacements) {
  const lang = getLanguage();
  if ( !supportedLanguages.includes(lang) ) return {};

  replacements = replacements || {};

  // Translate
  // template = translations[lang].default[template] || template;
  template = translations[lang][template] || template;

  // Replace
  return template.replace(/{([^}]+)}/g, function(_, key) {
    return replacements[key] || '{' + key + '}';
  }); 
}