import { domify } from 'min-dom';
import { is } from 'bpmn-js/lib/util/ModelUtil';

export function appendSendSmsElements(event, formData, selectedSmsSubjectLabel, selectedElement, elementFactory, elementRegistry, modeling, autoPlace) {
    const processElement = elementRegistry.find(element => is(element, 'bpmn:Process'));
    if (!processElement) {
      alert("Could not find the Process element");
      return;
    }

    if (!formData || !formData.smsSubject) {
      return;
    }

    const outgoingFlow = selectedElement.outgoing && selectedElement.outgoing.length && selectedElement.outgoing.length === 1 && selectedElement.outgoing[0];
    const outgoingShape = is(outgoingFlow, 'bpmn:SequenceFlow') && outgoingFlow.target;

    var setSmsVarsScriptTask = null;
    if (formData.smsSubject === 'smsBody') {
        setSmsVarsScriptTask = elementFactory.createShape({ type: 'bpmn:ScriptTask', name: 'Set_Sms_Vars_Task_1'});
        modeling.updateProperties(setSmsVarsScriptTask, { name: 'Set Sms Variables', script: `def evaluation = ""
    if (evaluationResult == "APPROVED")
        evaluation = "Onaylanmıştır"
    else
        evaluation = "Onaylanmamıştır"
    
    def jsonSlurper = new groovy.json.JsonSlurper()
    
    def smsBody = jsonSlurper.parseText(smsBody.toString())
    
    smsBody['bodyParams'][0]['value'] = initiatorFullName
    smsBody['bodyParams'][1]['value'] = evaluation
    
        execution.setVariable("smsBody",groovy.json.JsonOutput.toJson(${formData.smsSubject}))` });
    }

    const serviceTaskLabel = `Send ${selectedSmsSubjectLabel} Sms ServiceTask`;
    const serviceTask = elementFactory.createShape({ type: 'bpmn:ServiceTask', name: 'Task_1'});
    modeling.updateProperties(serviceTask, { name: serviceTaskLabel });

    const serviceTaskCamundaExpr = `\$\{flowRestCallComponent.init(execution).execute(\'http://notification/api/sms/sms\', \'POST\' , ${formData.smsSubject})\}`;
    modeling.updateProperties(serviceTask, { "camunda:expression": serviceTaskCamundaExpr });

    // Add the new service task shape to the diagram using `appendShape` to connect it to an existing shape
    if (autoPlace) {
      if (setSmsVarsScriptTask) {
        autoPlace.append(selectedElement, setSmsVarsScriptTask, { x: 400, y: 100 }, processElement);
        autoPlace.append(setSmsVarsScriptTask, serviceTask, { x: 400, y: 100 }, processElement);
      } else {
        autoPlace.append(selectedElement, serviceTask, { x: 400, y: 100 }, processElement);
      }
    } else {
      if (setSmsVarsScriptTask) {
        modeling.appendShape(selectedElement, setSmsVarsScriptTask, { x: 400, y: 100 }, processElement);
        modeling.appendShape(setSmsVarsScriptTask, serviceTask, { x: 400, y: 100 }, processElement);
      } else {
        modeling.appendShape(selectedElement, serviceTask, { x: 400, y: 100 }, processElement);
      }
    }

    if (setSmsVarsScriptTask) {
      modeling.createConnection(setSmsVarsScriptTask, serviceTask, { type: 'bpmn:SequenceFlow' }, processElement);
    }

    if (outgoingShape) {
      modeling.removeConnection(outgoingFlow);
      modeling.createConnection(serviceTask, outgoingShape, { type: 'bpmn:SequenceFlow' }, processElement);
    }
  
   // create.start(event, shape);

}

export function appendPlateauServiceTask(event, formData, selectedServiceLabel, selectedElement, elementFactory, elementRegistry, modeling, autoPlace) {
    const processElement = elementRegistry.find(element => is(element, 'bpmn:Process'));
    if (!processElement) {
      alert("Could not find the Process element");
      return;
    }

    if (!formData || !formData.service) {
      return;
    }

    const outgoingFlow = selectedElement.outgoing && selectedElement.outgoing.length && selectedElement.outgoing.length === 1 && selectedElement.outgoing[0];
    const outgoingShape = is(outgoingFlow, 'bpmn:SequenceFlow') && outgoingFlow.target;

    const serviceTaskLabel = `Call ${selectedServiceLabel} Service`;
    const serviceTask = elementFactory.createShape({ type: 'bpmn:ServiceTask', name: 'Task_1'});
    modeling.updateProperties(serviceTask, { name: serviceTaskLabel });

    modeling.updateProperties(serviceTask, { "camunda:expression": formData.service });

    // Add the new service task shape to the diagram using `appendShape` to connect it to an existing shape
    if (autoPlace) {
      autoPlace.append(selectedElement, serviceTask, { x: 400, y: 100 }, processElement);
    } else {
      modeling.appendShape(selectedElement, serviceTask, { x: 400, y: 100 }, processElement);
    }

    if (outgoingShape) {
      modeling.removeConnection(outgoingFlow);
      modeling.createConnection(serviceTask, outgoingShape, { type: 'bpmn:SequenceFlow' }, processElement);
    }
  
   // create.start(event, shape);
}