import BpmnModdle from 'bpmn-moddle'
import camundaModdle from 'camunda-bpmn-moddle/resources/camunda'
import { getProcessVariables/*, getVariablesForScope*/ } from '@stechquick/extract-all-process-variables'

const extractProcessVariables = async (bpmnXml) => {
  if (bpmnXml && bpmnXml.length > 0) {
    // console.log(`bpmnXml length: ${bpmnXml.length}`);
    const moddle = new BpmnModdle({ camunda: camundaModdle });
    var bpmnDefs = null;

    await moddle.fromXML(bpmnXml).then(({ rootElement: definitions, warnings }) => {
      if (warnings && warnings.length) console.log('import warnings', warnings);
      bpmnDefs = definitions;
    })

    const rootElementBO = bpmnDefs.get('rootElements')[0]
    const allVariables = getProcessVariables(rootElementBO).filter(v => !!v.name && !!v.type)
    // console.log(`Process Variables: ${JSON.stringify(allVariables)}`);

    return allVariables;
  }

  return null;
}

export {extractProcessVariables};