import entryFactory from 'bpmn-js-properties-panel/lib/factory/EntryFactory';
import { getBusinessObject } from 'bpmn-js/lib/util/ModelUtil';

import {
  is
} from 'bpmn-js/lib/util/ModelUtil';

import cmdHelper from '../../../helper/CmdHelper';

export default function(group, element, translate) {

  // Only return an entry, if the currently selected
  // element is a start event.

  if (is(element, 'bpmn:StartEvent')) {
    var selectOptions = [
      { value: 'OptionValue1', name: translate('Opsiyon Label 1') },
      { value: 'OptionValue2', name: translate('Opsiyon Label 2') },
      { value: 'OptionValue3', name: translate('Opsiyon Label 3') }
    ];

    group.entries.push(entryFactory.selectBox(translate, {
      id : 'restApiMethods',
      description : 'Select a Rest API Method',
      label : translate('Rest Api Method'),
      selectOptions: selectOptions,
      modelProperty : 'restApiMethod',

      get: function(element, node) {
        var bo = getBusinessObject(element);
        const restApiMethod = bo.get('plateau:restApiMethod') || '';

        return {
          restApiMethod: restApiMethod
        }
      },

      set: function(element, values, node) {
        const restApiMethod = values.restApiMethod;
        var bo = getBusinessObject(element);
        return cmdHelper.updateBusinessObject(element, bo, {restApiMethod: restApiMethod});
      }
    }));
  }
}