import { Form } from '@bpmn-io/form-js-viewer';
import { closeLightbox, NOTICE_MARKUP, openLightbox } from '../util/Lightbox';

export function openPlateauForm(opts) {
  let defaults = {
    lang: "en",
    schema: {},
    data: {},
    onSubmit: (event) => {}
  }
  
  let options = Object.assign(defaults, opts);

  options.containerSelector = options.containerSelector || '#bjs-lightbox-content';

  let formContent = async () => {
    const form = new Form({
      container: document.querySelector(options.containerSelector)
    });

    if (options.lang && options.lang !== 'en') {
      const validator = form.get('validator');
      if (options.lang === 'tr') {
        validator.validateField = validateFieldTr;
      }
    }
  
    await form.importSchema(options.schema, options.data);
    
    const fjsPoweredBy = document.querySelector('.fjs-powered-by');
    if (fjsPoweredBy) {
      fjsPoweredBy.classList.add('hidden');
    }

    const fjsButtons = document.querySelector('.fjs-button');
    if (fjsButtons) {
      fjsButtons.classList.add('right');
    }

    form.on('submit', (event) => {
      event.preventDefault();
      event.stopPropagation();

      const errors = form.validate();
      if (Object.keys(errors).length) {
        return;
      }
      console.log(event.data, event.errors);

      closeLightbox();
      
      if (typeof options.onSubmit === 'function') {
        options.onSubmit(event);
      }
    });
    
    // close quality assurance if user presses escape
    const fjsForm = document.querySelector('.fjs-form');
    fjsForm.addEventListener('keydown', (event) => {
      if (event.key === 'Escape') {
        closeLightbox();
      }
    });
  }

  // openLightbox(NOTICE_MARKUP);
  openLightbox(formContent);
}


export const validateFieldTr = (field, value) => {

    const { validate } = field;

    let errors = [];

    if (!validate) {
      return errors;
    }

    if (validate.pattern && value && !new RegExp(validate.pattern).test(value)) {
      errors = [
        ...errors,
        `${ validate.pattern } formatına uygun olmalı.`
      ];
    }

    if (validate.required && (typeof value === 'undefined' || value === '')) {
      errors = [
        ...errors,
        'Zorunlu alan.'
      ];
    }

    if ('min' in validate && value && value < validate.min) {
      errors = [
        ...errors,
        `En az ${ validate.min } olmalı.`
      ];
    }

    if ('max' in validate && value && value > validate.max) {
      errors = [
        ...errors,
        `En fazla ${ validate.max } olmalı.`
      ];
    }

    if ('minLength' in validate && value && value.trim().length < validate.minLength) {
      errors = [
        ...errors,
        `Uzunluk en az ${ validate.minLength } olmalı.`
      ];
    }

    if ('maxLength' in validate && value && value.trim().length > validate.maxLength) {
      errors = [
        ...errors,
        `Uzunluk en fazla ${ validate.maxLength } olmalı.`
      ];
    }

    return errors;
  }