import inherits from 'inherits';

import BpmnModeler from 'bpmn-js/lib/Modeler';

import { assign, debounce } from 'min-dash';

import propertiesPanelModule from 'bpmn-js-properties-panel';
import propertiesProviderModule from 'bpmn-js-properties-panel/lib/provider/camunda';
import tokenSimulationModule from 'bpmn-js-token-simulation';
import plateauAcceleratorPropertiesProviderModule from './provider/plateauAccelerator';
import plateauModdleDescriptor from './descriptors/plateau';

import camundaModdleDescriptor from 'camunda-bpmn-moddle/resources/camunda.json';
import { setCurrBpmnFileName, getCurrBpmnFileName } from './util'

//import templates from '../resources/templates.json';
import ruledTemplates from '../resources/plateau-accelerator-tmpl-001.json';

var templateLoader = require("bpmn-js-properties-panel/lib/provider/camunda/element-templates/");

import { filterRuledTemplates } from './appliesToExtended';
import plateauControlsModule from './plateau';
import { getLanguage, setLanguage, supportedLanguages } from './config';
import customTranslate from './i18n/customTranslate';
import { setSimulatorLang } from './i18n/tokenSimulationTranslation';
import { extractProcessVariables } from './extractProcessVariables';


// the diagram to display
// you may load it asynchronously via ajax, too
const initialDiagramXML = "<?xml version=\"1.0\" encoding=\"UTF-8\"?>\r\n<bpmn:definitions xmlns:xsi=\"http://www.w3.org/2001/XMLSchema-instance\" xmlns:bpmn=\"http://www.omg.org/spec/BPMN/20100524/MODEL\" xmlns:bpmndi=\"http://www.omg.org/spec/BPMN/20100524/DI\" xmlns:dc=\"http://www.omg.org/spec/DD/20100524/DC\" targetNamespace=\"http://bpmn.io/schema/bpmn\" id=\"Definitions_1\">\r\n  <bpmn:process id=\"Process_1\" isExecutable=\"false\">\r\n    <bpmn:startEvent id=\"StartEvent_1\"/>\r\n  </bpmn:process>\r\n  <bpmndi:BPMNDiagram id=\"BPMNDiagram_1\">\r\n    <bpmndi:BPMNPlane id=\"BPMNPlane_1\" bpmnElement=\"Process_1\">\r\n      <bpmndi:BPMNShape id=\"_BPMNShape_StartEvent_2\" bpmnElement=\"StartEvent_1\">\r\n        <dc:Bounds height=\"36.0\" width=\"36.0\" x=\"173.0\" y=\"102.0\"/>\r\n      </bpmndi:BPMNShape>\r\n    </bpmndi:BPMNPlane>\r\n  </bpmndi:BPMNDiagram>\r\n</bpmn:definitions>";

// Our custom translation module
// We need to use the array syntax that is used by bpmn-js internally
// 'value' tells bmpn-js to use the function instead of trying to instanciate it
var customTranslateModule = {};
if ( ['en', 'tr'].includes(getLanguage()) ) {
customTranslateModule.translate= [ 'value', customTranslate ];
propertiesPanelModule.__depends__.push({ 'translate': ['value', customTranslate] });
}

var DEFAULT_OPTIONS = {
    container: '#js-canvas',
    keyboard: {
      bindTo: window
    },
    height: 500,
    propertiesPanel: {
        parent: '#js-properties-panel'
    },
    additionalModules: [
        propertiesPanelModule,
        propertiesProviderModule,
        plateauAcceleratorPropertiesProviderModule,
        templateLoader.elementTemplatesLoader,
        tokenSimulationModule,
        customTranslateModule,
        plateauControlsModule
    ],
    elementTemplates: ruledTemplates,
    moddleExtensions: {
        camunda: camundaModdleDescriptor,
        plateau: plateauModdleDescriptor,
    }
}

var processDesignerIsReady_alreadySent = false;


function Modeler(options) {
    
  options = assign({}, DEFAULT_OPTIONS, options);
  BpmnModeler.call(this, options);
    
  this.diagramXML = initialDiagramXML;
  if (options.model && options.model.length && typeof options.model === 'string') {
    this.diagramXML = options.model;
  }
  
  if ( options.lang && supportedLanguages.includes(options.lang) ) setLanguage(options.lang);

  //const propsPanel = this.get('propertiesPanel');
  //const commandStack = viewer.get('commandStack');
    
  this.setImportDoneHandler();
  this.setSimulatorLangHandler();
  this.importDiagramXML(this.diagramXML);

  this._isModified = false;
  this.on('commandStack.changed', () => {this.exportArtifactsViaLink(this)});
  this.on('commandStack.changed', () => {this._isModified = true});

  setSimulatorLang(getLanguage());

}
    
inherits(Modeler, BpmnModeler);

Modeler.prototype.isModified = function() {
  return this._isModified;
}

Modeler.prototype.setImportDoneHandler = function () {
  const viewer = this;
  const eventBus = viewer.get('eventBus');
  eventBus.on('import.done', function(e) {
    console.log('Diagram has been imported.');
    viewer._isModified = false;
    viewer.exportArtifactsViaLink(viewer);
    if (window.parent && !processDesignerIsReady_alreadySent) {
      processDesignerIsReady_alreadySent = true;
      viewer.send({type: 'ProcessDesignerIsReady', content: viewer.diagramXML});
    }
  });
}

Modeler.prototype.setSimulatorLangHandler = function () {
  const viewer = this;
  const eventBus = viewer.get('eventBus');
  const toggleSimulationContainer = document.querySelector('.toggle-mode');
  if (!toggleSimulationContainer) return;

  toggleSimulationContainer.onclick = (e) => setSimulatorLang(getLanguage());

  eventBus.on('tokenSimulation.simulator.elementChanged', (e) => setSimulatorLang(getLanguage()));
  eventBus.on('tokenSimulation.simulator.createScope', (e) => setSimulatorLang(getLanguage()));
  eventBus.on('tokenSimulation.playSimulation', (e) => setSimulatorLang(getLanguage()));
  eventBus.on('tokenSimulation.simulator.destroyScope', (e) => setSimulatorLang(getLanguage()));
}

Modeler.prototype.getDiagramXML = async function() {
  const viewer = this;
  const { xml } = await viewer.saveXML({ format: true });
  return xml;
}

Modeler.prototype.importDiagramXML= function(pDiagramXML) {
  var viewer = this;

  if (pDiagramXML && pDiagramXML.length) {
    viewer.diagramXML = pDiagramXML;
  } else if (!viewer.diagramXML || viewer.diagramXML.length < 1) {
    viewer.diagramXML = initialDiagramXML;
    // viewer.clear();
    return;
  }

  viewer.importXML(viewer.diagramXML).then(function(){

    // diagram is loaded, add interaction to it

    // you may hook into any of the following events
    var events = [
      'element.click',
      'element.dblclick'
    ];

    const eventBus = viewer.get('eventBus');

    events.forEach(function(event) {

      eventBus.on(event, function(e) {
        viewer.send({type: 'clickMessage', msg: `${String(event)} on ${e.element.type} [${e.element.id}]`, initiator: 'ProcessDesigner'});
      });
    });

    const applyRuledTepmlatesToElement = e => {
      if (!['bpmn:StartEvent', 'bpmn:CallActivity', 'bpmn:SequenceFlow']
        .includes(e.element.type)) return

      // access elementTemplateLoader
      var loader = viewer.get("elementTemplatesLoader");
      // add the new template to the list of all templates
      const newTemplates = filterRuledTemplates(e.element.businessObject, ruledTemplates);
      loader.setTemplates(newTemplates);
      // don't reload templates as it would reinitialize them..
      // loader.reload();
    }

    eventBus.on('element.click', applyRuledTepmlatesToElement);
    eventBus.on('element.changed', applyRuledTepmlatesToElement);
    
  }).catch(function(err) {

    console.error('failed to load diagram');
    console.error(err);

  });
}

Modeler.prototype.exportArtifactsViaLink = async function(viewer) {
  const xxx = viewer.getDiagramXML();
  
  //debounce(async function() {
    var moduleAndFileName = getCurrBpmnFileName()
    if (!moduleAndFileName || moduleAndFileName !== 'string' || moduleAndFileName.length < 1) moduleAndFileName = 'diagram.bpmn'

    const downloadLink = document.querySelector('#js-download-diagram');
    if (!downloadLink) return;

    const setEncoded = function(link, name, data) {
      var encodedData = encodeURIComponent(data);

      link.setAttribute('href', 'data:application/bpmn20-xml;charset=UTF-8,' + encodedData)
      link.setAttribute('download', name)

      if (data) {
        link.classList.add('active');
      } else {
        link.classList.remove('active');
      }
    }

    try {
      const { xml } = await viewer.saveXML({ format: true });
      viewer.diagramXML = xml;
      // send({type: 'ContentChanged', fileName: moduleAndFileName, content: diagramXML});
      setEncoded(downloadLink, moduleAndFileName, xml);
    } catch (err) {
      console.error('Error happened during downloading XML');
      console.error(err);
      setEncoded(downloadLink, moduleAndFileName, null);
    }
  //}, 500);
}

Modeler.prototype.send= function (msg) {
  const targetWindow = parent;
  const data = {type: msg.type, initiator: 'ProcessDesigner', msg};
  targetWindow.postMessage(data, "*");
  console.log('Message sent : ' + JSON.stringify(data));
}

function exportTest() {
  return {x: 1, y: "A"};
}

export default Modeler;
export { exportTest, extractProcessVariables };