import { isObject, isIn } from './util';

const isRule = (value) => 
    isObject(value) 
        && ('rule' in value) && isIn(value['rule'], 'and', 'or') 
        && ('ruleArgs' in value) && Array.isArray(value['ruleArgs']);

const isRuleCondition = (value) => 
    isObject(value) 
    && 'propType' in value && isIn(value['propType'], 'attr', 'selector')
    && 'attr' in value
    && 'filterFn' in value
    && 'filterFnArg' in value
    && ( value['propType'] !=='selector' || 'selector' in value );

const isValidFilterFn = (filterFn) => isIn(filterFn, '===', 'contains', 'startsWith', 'N/A');

const is_FilterFn_Valid_For_AttrVal = (attrVal, filterFn) => {
    if ( filterFn === '===') {
        return true;
    } else if ( filterFn === 'N/A' ) {
        return true;
    } else if ( filterFn === 'contains' ) {
        return (typeof attrVal === 'string');
    } else if ( filterFn === 'startsWith' ) {
        return (typeof attrVal === 'string');
    }
}

const applyFilterFnToVal = (val, filterFn, filterFnArg) => {
    if ( filterFn === '===') {
        return (val === filterFnArg);
    } else if ( filterFn === 'N/A' ) {
        return true;
    } else if ( filterFn === 'contains' ) {
        return ( val.indexOf( filterFnArg ) > -1 );
    } else if ( filterFn === 'startsWith' ) {
        return ( val.startsWith( filterFnArg ));
    }
}

function getBoAttrAtAttrSteps(bo, attr) {
    const attrSteps = attr.split(/\s+/);
    let boAttr = bo;
    attrSteps.forEach(attrStep => {
        if ( !bo ) { return; }

        if ( attrStep.indexOf('.') < 0 ) {
            if ( Array.isArray(boAttr) ) {
                if (boAttr.length !== 1) { 
                    boAttr = undefined;
                } else {
                    boAttr = boAttr[0];
                }
            } else {
                boAttr = boAttr[attrStep];
            }
        } else {
            const attrStepEvalStr = attrStep
                .replace(/[\(\)\[\]]/g, '') // sanitize parens, brackets)
                .replace(/\.and\./g, ' && ')  // replace ".and." with "&&"
                .replace(/\.or\./g,  ' || ')  // replace ".or." with "||"
                .replace(/(\S+)\.===\.(\S+)/g, 'boAttr[\'$1\']===\'$2\'')
                .replace(/(\S+)\.contains\.(\S+)/g, 'boAttr[\'$1\'].contains(\'$2\')')
                .replace(/(\S+)\.startsWith\.(\S+)/g, 'boAttr[\'$1\'].startsWith(\'$2\')')
                ;

            // TODO Burada array ya da object olmasina gore iki farkli sekilde ilerleyecegiz.
            //      array ise filter() icinde eval() ile boAttr yi daraltacagiz, 
            //      object ise eval() sonucu 
            //          true ise loop' a devam 
            //          false ise boAttr = undefined and return from loop.  
            if ( isObject(boAttr) ) {
                const filterSatistied = eval(attrStepEvalStr);
                if ( !filterSatistied ) {
                    boAttr = undefined;
                    return;
                }
            } else if ( Array.isArray(boAttr) ) {
                const attrStepFilterFnEvalStr = attrStepEvalStr.replace(/boAttr\[/g, 'boAttrElem[');
                boAttr = boAttr.filter( (boAttrElem) => eval(attrStepFilterFnEvalStr) );
                if ( !boAttr || boAttr.length < 1) {
                    boAttr = undefined;
                    return false;
                }
            }
        }
        
        if (!boAttr) { return false; }
    });

    return boAttr;
}

export const appliesToExtended = (bo, extendedRule) => {
    if (!bo) { return false };
    if ( !isRule(extendedRule) ) { return false };

    switch(extendedRule['rule']) {
        case 'and' : {
            const ruleArgs = extendedRule['ruleArgs'];
            for(let i=0; i < ruleArgs.length; i++) {
                const arg = ruleArgs[i];

                if ( isRule(arg) ) { 
                    const subResult = appliesToExtended(bo, arg);
                    if ( !subResult ) { return false; }
                } else if ( isRuleCondition(arg) ) {
                    if ( 'attr' === arg['propType']) {
                        const {attr, filterFn, filterFnArg} = arg;

                        let boAttr = getBoAttrAtAttrSteps(bo, attr);
                        if ( !boAttr ) { return false; }

                        if ( !isValidFilterFn(filterFn) ) { return false; }
                        if ( !is_FilterFn_Valid_For_AttrVal(boAttr, filterFn) ) { return false; }

                        if ( !applyFilterFnToVal(boAttr, filterFn, filterFnArg) ) { return false; }
                    } else if ( 'selector' === arg['propType'] ) {
                        return false;
                    }
                } else {
                    return false;
                }
            };

            return true;
        }
        case 'or' : {
            const ruleArgs = extendedRule['ruleArgs'];
            for(let i=0; i < ruleArgs.length; i++) {
                const arg = ruleArgs[i];

                if ( isRule(arg) ) {
                    const subResult = appliesToExtended(bo, arg);
                    if ( subResult ) { return true; }
                } else if ( isRuleCondition(arg) ) {
                    if ( 'attr' === arg['propType']) {
                        const {attr, filterFn, filterFnArg} = arg;

                        let boAttr = getBoAttrAtAttrSteps(bo, attr);
                        if ( !boAttr ) { return false; }

                        if ( !isValidFilterFn(filterFn) ) { return false; }
                        if ( !is_FilterFn_Valid_For_AttrVal(boAttr, filterFn) ) { return false; }

                        if ( applyFilterFnToVal( boAttr, filterFn, filterFnArg) ) { return true; }
                    } else if ( 'selector' === arg['propType'] ) {
                        return false;
                    }
                } else {
                    return false;
                }
            };

            return false;
        }
    }

    return false;
};

export function filterRuledTemplates(bo, ruledTemplates) {
    if (!bo || !Array.isArray(ruledTemplates) || ruledTemplates.length < 1) return [];

    return ruledTemplates.filter(rt => {
        return rt.appliesTo.some(at => at === bo.$type) && appliesToExtended(bo, rt.appliesToExtended);
    });
}