// Require your custom property entries.
import restApiCallProps from './parts/RestApiCallProps';

var LOW_PRIORITY = 500;


// Create the custom magic tab.
// The properties are organized in groups.
function createPlateauAcceleratorTabGroups(element, translate) {

  // Create a group called "Rest Api Methods".
  var restApiGroup = {
    id: 'rest-api-methods',
    label: 'Rest Api Methods',
    entries: []
  };

  // Add the spell props to the rest api methods group.
  restApiCallProps(restApiGroup, element, translate);

  return [
    restApiGroup
  ];
}

export default function PlateauPropertiesProvider(propertiesPanel, translate) {

  // Register our custom plateau properties provider.
  // Use a lower priority to ensure it is loaded after the basic BPMN properties.
  propertiesPanel.registerProvider(LOW_PRIORITY, this);

  this.getTabs = function(element) {

    return function(entries) {

      // Add the "Plateau Accelerator" tab
      var plateauAcceleratorTab = {
        id: 'plateauAccelerator',
        label: 'Plateau Accelerator',
        groups: createPlateauAcceleratorTabGroups(element, translate)
      };

      entries.push(plateauAcceleratorTab);
  
      return entries;
    }
  };
}

PlateauPropertiesProvider.$inject = [ 'propertiesPanel', 'translate' ]